.guessedWord{ letter-spacing: 20px; }

.Hangman {
    width: 400px;
    color: black;
    text-align: center;
    zoom: 1.1;
  }
  
  .Hangman img {
    border: 10px solid white;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
      0 0 40px rgba(0, 0, 0, 0.06) inset;
  }
  
  .Hangman h1 {
    font-size: 4rem;
    font-weight: 100;
    margin-bottom: 0.1em;
    margin-top: 0;
  }
  
  .Hangman-word {
    letter-spacing: 1em;
    margin: 0.4em -1em 0.2em 0;
    font-size: 2rem;
  }
  
  .Hangman-btns {
    text-align: center;
    display: inline-block;
    width: 250px;
    margin-top: 0;
  }
  .Hangman button {
    border: none;
    font-family: inherit;
    font-size: 1rem;
    cursor: pointer;
    width: 30px;
    margin: 5px 2px 2px 2px;
    letter-spacing: 1px;
    outline: none;
    background: #00bcd4;
    color: #fff;
    box-shadow: 0 6px #0097a7;
    border-radius: 5px;
    padding-top: 4px;
  }
  
  .Hangman button:hover {
    background-color: #0097a7;
  }
  
  .Hangman button:disabled {
    background: #bdbdbd;
    color: #eeeeee;
    box-shadow: 0 6px #9e9e9e;
  }
  #reset {
    width: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    background: #9c27b0;
    box-shadow: 0 6px #7b1fa2;
    border-radius: 0px;
    font-family: arial;
    font-weight: bold;
    font-size: 22px;
    text-transform: uppercase;
  }
  #reset:hover {
    background: #7b1fa2;
  }
  